.cartitem {
    display: flex;
    margin-bottom: 10px;

    .img_container {
        width: 30%;
        margin-right: 20px;
        border-radius: 10px;
        height: 85px;

        img {
            width: 100%;
            height: 100%;
            border-radius: 10px;
        }

        @media screen and (max-width:1500px) {
            width: 50%;
        }

        @media screen and (max-width:1200px) {
            width: 20%;
            height: 100px;
        }

        @media screen and (max-width:576px) {
            width: 30%;
            height: 80px;
        }

        @media screen and (max-width:420px) {
            width: 50%;
            height: 80px;
        }
    }

    .item_content {
        padding: 0.5em 0;
        width: 100%;
        display: flex;
        flex-direction: column;
        justify-content: space-between;

        p {
            font-weight: 500;
            font-size: 17px;

        }

        .price_quantity {
            display: flex;
            align-items: center;
            justify-content: space-between;
            width: 100%;

            div:first-child {
                color: rgb(255, 123, 0);
                font-size: 21px;
                font-weight: 700;

                @media screen and (max-width:576px) {
                    font-size: 18px;
                }
            }

            button {
                background-color: rgb(255, 123, 0);
                color: white;
                font-size: large;
                font-weight: 700;
                width: 25px;
                cursor: pointer;
                border-radius: 5px;
            }

            button:first-child {
                margin-right: 1em;
            }

            button:last-child {
                margin-left: 1em;
            }
        }
    }
}